<template>
	  <transition name="modal">
    <div class="modal-mask" v-if="show">
      <div class="modal-wrapper">
        <div class="modal-container">
        	<span class="modal-close" @click="$emit('close')">X</span>
          <div  class="modal-header" align="center">
            <p align="center">{{company.client.team_name}}</p>
          </div>
       
         <!-- {{all_transactions.length}} -->
          <div class="modal-body">
        <table class="table">
          <thead>
            <th>Transaction Type</th>
            <th>Amount</th>
            <th>Description</th>
            <th>Date</th>
          </thead>
          <tbody>
            <tr v-for="transaction in transactions">
              <td><span>{{transaction.type}}</span></td>
                <td><span>{{transaction.amount}}</span></td>
                  <td><span>{{transaction.tag}}</span></td>
                    <td><span>{{transaction.created_time}}</span></td>
            </tr>
            <tr v-if="transactions.length==0 && !isLoading">
              <td colspan="4" align="center">
                <span>No transaction found</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="col-md-12" align="center" v-show="!isLoading &&transactions.length>0">
      <button class="btn btn-primary my-btn-skill" @click="p(transactions[0]._id)" ><i class="ion-arrow-left-c"></i></button>
    <button class="btn btn-primary my-btn-skill" @click="n(transactions[transactions.length-1]._id)"><i class="ion-arrow-right-c"></i></button>
    </div>
          </div>
          <!-- <div class="" > -->
      <div  class="loading-spinner" v-if="isLoading"  >
        <div class="dot dotOne"></div>
        <div class="dot dotTwo"></div>
        <div class="dot dotThree"></div>
      </div>
    <!-- </div> -->
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/javascript">
import Spinner from '@/components/spinner';
import {GET_TRANSACTION,PREVIOUS_TRANSACTIONS} from '../mutation-types'
import {mapGetters,mapActions} from 'vuex';
	export default{
	props:['show','company'],
	mounted(){
   
	},
  data(){
    return {
      p_count:0,
      n_count:0
    }
  },
	components:{
		Spinner
	},
	computed:{
      ...mapGetters({
      'isLoading':'isGettingTransaction'
      }),
      all_transactions(){
        return this.$store.getters.all_transactions(this.company.client.Id)
      },
      transactions(){
        return this.$store.getters.getTransactions(this.company.client.Id)
      },
      hide(){
        console.log(this.all_transactions.indexOf(this.transactions[0]));
        return this.all_transactions.indexOf(this.transactions[0])>0;
      }
	},
	methods:{
   n(id){
    var self = this;
    console.log(id)
    this.$store.dispatch(GET_TRANSACTION,{id:id,team_Id:self.company.client.Id})
     console.log("next",self.n_count++)
   },
    p(id){
    var self = this;
    // console.log(id)
    if(this.all_transactions[0]._id==id)
      return;
    console.log("previous",self.p_count++)
    this.$store.dispatch(PREVIOUS_TRANSACTIONS,{id:id,team_Id:self.company.client.Id})
   }
	}
	}
</script>
<style type="text/css" scoped>

  .loading-spinner {
    position : absolute;
    top      : 40%;
    left     : 50%;
    -webkit-transform : translateX(-50%) translateY(-50%);
    -moz-transform : translateX(-50%) translateY(-50%);
    transform : translateX(-50%) translateY(-50%);
  }
  .dot {
    background    : black;
    border-radius : 100%;
    color         : white;
    height        : 8px;
    line-height   : 8px;
    text-align    : center;
    width         : 8px;
  }
  .dotOne {
    -webkit-animation : dotOneKeyframes 5s ease  infinite;
    background        : #007bff;
    position          : absolute;
    top               : 0;
    left              : 0;
  }
  .dotTwo {
    -webkit-animation : dotTwoKeyframes 5s ease 0.4166666666666s infinite;
    background        : #007bff;
    position          : absolute;
    top               : 0;
    left              : 14px;
  }
  .dotThree {
    -webkit-animation : dotThreeKeyframes 5s ease 0.83333333333s infinite;
    background        : #007bff;
    position          : absolute;
    top               : 14px;
    left              : 14px;
  }
  @-webkit-keyframes dotOneKeyframes {
    0% {
      top  : 0;
      left : 0;
    }
    8.3333333333% {
      top  : 14px;
      left : 0;
    }
    16.6666666666% {
    }
    25% {
      top  : 14px;
      left : 0;
    }
    33.3333333333% {
      top  : 14px;
      left : 14px;
    }
    41.6666666666% {
    }
    50% {
      top  : 14px;
      left : 14px;
    }
    58.3333333333% {
      top  : 0;
      left : 14px;
    }
    66.6666666666% {
    }
    75% {
      top  : 0;
      left : 14px;
    }
    83.3333333333% {
      top  : 0;
      left : 0;
    }
    91.6666666666% {
    }
    100% {
    }
  }
  @-webkit-keyframes dotTwoKeyframes {
    0% {
      top  : 0;
      left : 14px;
    }
    8.3333333333% {
      top  : 0;
      left : 0;
    }
    16.6666666666% {
    }
    25% {
      top  : 0;
      left : 0;
    }
    33.3333333333% {
      top  : 14px;
      left : 0;
    }
    41.6666666666% {
    }
    50% {
      top  : 14px;
      left : 0;
    }
    58.3333333333% {
      top  : 14px;
      left : 14px;
    }
    66.6666666666% {
    }
    75% {
      top  : 14px;
      left : 14px;
    }
    83.3333333333% {
      top  : 0;
      left : 14px;
    }
    91.6666666666% {
    }
    100% {
    }
  }
  @-webkit-keyframes dotThreeKeyframes {
    0% {
      top  : 14px;
      left : 14px;
    }
    8.3333333333% {
      top  : 0;
      left : 14px;
    }
    16.6666666666% {
    }
    25% {
      top  : 0;
      left : 14px;
    }
    33.3333333333% {
      top  : 0;
      left : 0;
    }
    41.6666666666% {
    }
    50% {
      top  : 0;
      left : 0;
    }
    58.3333333333% {
      top  : 14px;
      left : 0;
    }
    66.6666666666% {
    }
    75% {
      top  : 14px;
      left : 0;
    }
    83.3333333333% {
      top  : 14px;
      left : 14px;
    }
    91.6666666666% {
    }
    100% {
    }
  }
.my-btn{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	width: 30%;


}
.docker-content{
	background-color: #000 !important;
	color: #fff !important;
	/*font-family: italics*/
}
.modal-close{
	float:right;
	color:#000;
	/*border-radius: 20px;*/
	cursor: pointer !important;
	/*background-color: #000*/
		padding:5px;
}
.modal-close:hover{
	box-shadow: 1px 1px 8px #ccc ;


}
.vue-notification .success{
	background-color: #fff !important;
	padding:10px !important;
	box-shadow: 1px 1px 8px #ccc !important;
}
	.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 80%;
  min-height: 60%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
  margin-top: -10%;
}

.modal-header h5 {
  margin-top: 0;
  /*color: #42b983;*/
 
}
.modal-header{
	 border-bottom: unset !important
}

.modal-body {
  /*margin: 20px 0;*/
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}
.modal-footer{
	border-top: unset !important;
}
.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>


