<template>
	<div align="center" class=" admin-header">
	
		<nav class="nav nav-pills nav-justified">
		
  <a class="mynav nav-item nav-link" :class="{'active':routeName=='clients'}"href="#/dashboard/clients">Manage Clients</a>
  <a class="mynav nav-item nav-link" :class="{'active':routeName=='docker'}" href="#/dashboard/docker">Manage Docker Files</a>
  <!-- <a class="mynav nav-item nav-link" :class="{'active':routeName=='transaction'}" href="#">Transaction History</a> -->
