     <a class="mynav nav-item nav-link" :class="{'active':routeName=='features'}" href="#/dashboard/features">Features Page</a>
     <a class="mynav nav-item nav-link" :class="{'active':routeName=='pricing'}" href="#/dashboard/pricing">Pricing Page</a>
