  <!-- <a class="nav-item nav-link disabled" href="#">Disabled</a> -->
</nav>
	</div>
</template>
<script type="text/javascript">
	export default{
		computed:{
			 routeName() {
       return this.$route.name
     },
		}

	}
</script>
<style type="text/css">
	.admin-header{
		padding: 20px;
		margin-top: 10px;
		
	}
	.mynav{
		background-color: #fff !important;
		color:#000 !important;
	}
	.active{
		border-radius: unset !important;
		border-bottom:3px solid #007bff !important;

	}
</style>


