<template>
<div class="container">
	<div class="row">
		<div class="col-md-4"></div>
		<div class="col-md-4">
			<br>
			<br>
			<br>
			<br>
			<br>
			<br>
			<br>
			<br>
			<br>
			<form @submit.prevent="validateBeforeSubmit">

				<div class="form-group">
					<p  align= "center" style="color:red">{{message}}</p>
					<!-- {{user}} -->
					<input type="text" placeholder="Name" v-model="user.username" class="form-control" v-validate="'required'" name="username" />
					<p style="color:red" v-show="errors.has('username')">username is required</p>
				</div>
				<div class="form-group">
					<input type="password" v-model="user.password" v-validate="'required'" placeholder="password" name="password"
					 class="form-control">
					 <p style="color:red" v-show="errors.has('password')">password is required</p>
				</div>
				<button  align="center" class="btn btn-primary my-btn">Login</button>
			</form>
		</div>
	</div>
</div>
</template>
<script type="text/javascript">
import {LOGIN} from '../mutation-types'
import {mapGetters,mapActions} from 'vuex';
	export default{
		name: 'login',

  components: {
  },
data(){
	return {
		user:{
			username : '',
			password : ''
		}
	}
},
computed:{
	...mapGetters([
		'message'
		])
},
  methods: {
  		validateBeforeSubmit(){
  				var self = this;
			self.$validator.validateAll()
			.then(result=>{
				if(result){
					this.$store.dispatch(LOGIN,{username:self.user.username,password:self.user.password})
				}
			})
  		},
	}

}
</script>
<style type="text/css">
	.my-btn{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	width: 30%;


}
</style>


