<template>
	  <transition name="modal">
    <div class="modal-mask" v-if="show">
      <div class="modal-wrapper">
        <div class="modal-container">
        	<span class="modal-close" @click="$emit('close')">X</span>
          <div  class="modal-header" align="center">
          </div>
       
<!-- {{docker}} -->
          <div class="modal-body">
       <i class="ion-person-stalker"></i> COMPANY: {{team.client.team_name}} <br>
        <i class="ion-person-stalker"></i> MEMBERS: {{team.team.length}}

         <table class="table">
           <thead>
             <th>Name</th>
             <th>Member type</th>
           </thead>
           <tbody class="team">
             <tr v-for="t in team.team">
               <td>{{fullname(t)}} <!-- <p  style="display:inline" v-if="fullname(t)=='Mr. Prince Ndako'">&#x1F601</p> --></td>
               <td style="font-size:20px!important">{{position(t.type)}}
                
               </td>
               <!-- <td> -->
                 <!-- image.im -->
               <!-- </td> -->
             </tr>
           </tbody>
         </table>
     <!--     <paginate
  :page-count="8"
  :prev-text="'Prev'"
  :next-text="'Next'"
  :container-class="'team'">
</paginate> -->
          </div>
        
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/javascript">
import Spinner from '@/components/spinner';
// import {NEW_DOCKER,REMOVE_DOCKER} from '../mutation-types'
// import {ADD_DOCKER} from '../api'
import {mapGetters} from 'vuex';
	export default{
	props:['show','team'],
	mounted(){
// alert(API)
	},
	components:{
		Spinner
	},
	computed:{

	},
	methods:{
     fullname(data){
      return data.first_name+" "+data.last_name;
    },
    position(type){
      // switch(type){
        var position;
        if(type=="1")
          position = "CLIENT";
        else if(type=="2")
          position = "PROJECT MANAGER"
        else if(type=="3")
          position = "DEVELOPER";
        return position
      // }
    },
		add(){
			var self = this;
			self.$validator.validateAll()
			.then(result=>{
				if(result){
					self.$store.dispatch(NEW_DOCKER,self.docker)
					.then(response=>{
						self.$show_notification(self.message);
						if(self.error){

						}
						else{
							self.$emit('close')
						}
					})
				}
			})

		}
	}
	}
</script>
<style type="text/css">
.my-btn{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	width: 30%;


}
.docker-content{
	background-color: #000 !important;
	color: #fff !important;
	/*font-family: italics*/
}
.modal-close{
	float:right;
	color:#000;
	/*border-radius: 20px;*/
	cursor: pointer !important;
	/*background-color: #000*/
		padding:5px;
}
.modal-close:hover{
	box-shadow: 1px 1px 8px #ccc ;


}
.vue-notification .success{
	background-color: #fff !important;
	padding:10px !important;
	box-shadow: 1px 1px 8px #ccc !important;
}
	.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 40%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h5 {
  margin-top: 0;
  /*color: #42b983;*/
 
}
.modal-header{
	 border-bottom: unset !important
}

.modal-body {
  /*margin: 20px 0;*/
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}
.modal-footer{
	border-top: unset !important;
}
.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>


