// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Login from '@/components/login'
import Dashboard from '@/components/dashboard'
import Header from '@/components/header'
import router from './router'
import VeeValidate from 'vee-validate';
import Vuex from 'vuex'
import Paginate from 'vuejs-paginate'
import axios from 'axios'
import VueAxios from 'vue-axios'
import {ADD_DOCKER,RETRIEVE_DOCKER,REMOVE_DOCKER,SKILLS,CLIENTS,TRANSACTION,CLIENT_LOGIN} from './api'
import Notifications from 'vue-notification'
import {NEW_DOCKER,DELETE_DOCKER,GET_DOCKER,
		ADD_SKILL,DELETE_CLIENT,
		GET_SKILLS,DELETE_SKILL,GET_CLIENTS,
		PREVIOUS,GET_TRANSACTION,PAGINATE_PREVIOUS,
		PREVIOUS_TRANSACTIONS,CLEAR_TRANSACTION,
		REFRESH_CLIENTS,FETCH_CLIENTS,IS_LOGGED_IN,
		CHECK_TOKEN,LOGIN,LOGOUT} from './mutation-types'
// import {NEW_DOCKER,DELETE_DOCKER,GET_DOCKER} from './mutation-types'
import features from '@/store/features'
import landing from '@/store/landing'
import faqs from '@/store/faqs'
import pricing from '@/store/pricing'
import createPersistedState from 'vuex-persistedstate';
import * as Cookies from 'js-cookie';
import ElementUI from 'element-ui'
import locale from 'element-ui/lib/locale/lang/en'
import 'element-ui/lib/theme-chalk/index.css'
require('bootstrap/dist/css/bootstrap.min.css')
import Editor from '@tinymce/tinymce-vue';


Vue.use(Notifications);
// import Store from '@/store'
Vue.use(Vuex)
Vue.use(VeeValidate);
Vue.use(VueAxios, axios)
Vue.component('paginate', Paginate)
Vue.use(ElementUI, { locale })
Vue.config.productionTip = false
 Vue.prototype.$show_notification = function(msg, type, duration){
      var data = {type: type || "success", title: msg};
      if(duration)
        data.duration = duration;

      Vue.prototype.$notify(data)
    };
/* eslint-disable no-new */
const store = new Vuex.Store({
	plugins: [
		createPersistedState({
	      key: 'dmsState',
	      getState: key => Cookies.getJSON(key),
	      setState: (key, state) => Cookies.set(key, state, { expires: 14 }),
	      paths: ['landing', 'features','pricing']
	    })
	],
	state:{
		dockers:[],
		skills:[],
		token:"",
		isLoading:false,
		message:"",
		error:false,
		clients:[],
		previous:'',
		all_clients:[],
		transactions:[],
		all_transactions:[],
		previous_transactions:[],
		isGettingTransaction:false,
		isLoggedIn:false
	}
	,
	getters:{
		getDockers:(state)=>{
			return state.dockers;
		},
		isGettingTransaction:(state)=>{
			return state.isGettingTransaction
		},
		getTransactions:(state)=>{
				return function(id){
			return state.transactions.filter(t=>t.Id==id)

			}
		},
		isLoggedIn:(state)=>{
			return state.isLoggedIn;
		},
		all_transactions:(state)=>{
			return function(id){
			return state.all_transactions.filter(t=>t.Id==id)

			}
		},
		getClients:(state)=>{
			return state.clients;
		},
		all_clients:(state)=>{
			return state.all_clients
		},
		getPrevious:(state)=>{
			return state.previous;
		},
		getSkills:(state)=>{
			return state.skills
		},
		isLoading:(state)=>{
			return state.isLoading
		},
		getToken:(state)=>{
			return state.token;
		},
		message:(state)=>{
			return state.message
		},
		error:(state)=>{
			return state.error
		}
	},
	mutations:{
		[DELETE_DOCKER](state,payload){
			state.dockers.splice(state.dockers.indexOf(payload),1)
		},
		[DELETE_CLIENT](state,payload){
			if(state.clients.indexOf(payload)>-1)
			state.clients.splice(state.clients.indexOf(payload),1)
			state.all_clients.splice(state.all_clients.indexOf(payload),1)
		},
		[GET_DOCKER](state,payload){
			state.dockers = payload
			// alert(payload)
		},
		[LOGIN](state,payload){
			state.isLoggedIn = payload.status;
			state.token = payload.token;
		},
		[IS_LOGGED_IN](state,payload){
			state.isLoggedIn = status;
		},
		[GET_TRANSACTION](state,payload){
			state.previous_transactions = state.transactions
			state.transactions = payload;
			state.all_transactions = state.all_transactions.concat(payload)
		},
		[GET_CLIENTS](state,payload){
			// console.log(payload)
			state.previous = state.clients;
			state.clients = payload;
			state.all_clients = state.all_clients.concat(payload)
			// alert(payload)
		},
		[NEW_DOCKER](state,payload){
			state.dockers.push(payload)
		},
		[ADD_SKILL](state,payload){
			state.skills.push(payload)
		},
		[GET_SKILLS](state,payload){
			state.skills = payload;
		},
		[DELETE_SKILL](state,payload){
			state.skills.splice(state.skills.indexOf(payload),1);
		},
		[PREVIOUS](state,payload){
			state.clients = payload;
		},
		[CLEAR_TRANSACTION](state,payload){
			state.transactions = [];
		},
		[LOGOUT](state,payload){
		state.dockers=[],
		state.skills=[],
		state.token="",
		state.isLoading=false,
		state.message="",
		state.error=false,
		state.clients=[],
		state.previous='',
		state.all_clients=[],
		state.transactions=[],
		state.all_transactions=[],
		state.previous_transactions=[],
		state.isGettingTransaction=false,
		state.isLoggedIn=false
		},
		[PREVIOUS_TRANSACTIONS](state,payload){
			state.transactions = payload;
		},
		loading(state,payload){
			state.isLoading = payload;
		},
		setMessage(state,payload){
			state.message = payload;
		},
		setError(state,error){
			state.error = error
		},
		transactions(state,payload){
			state.isGettingTransaction=payload
		}

	},
	actions:{
		[PAGINATE_PREVIOUS](context,payload){
			// console.log(payload)
			var previous;
				if(payload){
				var _client = payload['state'].find(c=>c.client._id==payload['id']);
			    if(_client){
			    	var index = payload['state'].indexOf(_client);
					var start;
					if(index==5)
						start = 0;
					else
						start = index-5;
					previous = payload['state'].filter(c=>c.client._id<payload['id']).splice(start,5);
				    context.commit(payload['commit'],previous);
				
			}
			}
		},
		[LOGOUT](context,payload){
			context.commit(LOGOUT);
			router.push("/")
		},
		[DELETE_DOCKER](context,payload){
			// context.commit(REMOVE_DOCKER);
			context.commit('loading',true)
			Vue.axios.post(REMOVE_DOCKER,
				payload,
				{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.commit(DELETE_DOCKER,payload);
				context.commit("setError",!response.data.status)
				context.commit("loading",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[PREVIOUS](context,payload){
			var _payload = {
				"id":payload.id,
				"state":context.state.all_clients,
				"commit":PREVIOUS
			}
			context.dispatch(PAGINATE_PREVIOUS,_payload)
		},
		[CLEAR_TRANSACTION](context,payload){
			context.commit(CLEAR_TRANSACTION);
		},
		[REFRESH_CLIENTS](context,payload){
			// context.dispatch(GET_CLIFEENTS,payload)
		},
		[PREVIOUS_TRANSACTIONS](context,payload){
				var previous = [];
				if(payload){
				var transactions = context.getters.all_transactions(payload.team_Id);
				var _transaction = transactions.find(t=>t._id==payload.id);
				var index = transactions.indexOf(_transaction);
					var records = [];
					var start;
					console.log(index);
					if(index==5)
						start = 0;
					else{
					start = index-5;
					}

					previous =  transactions.filter(t=>t._id<payload.id).splice(start,5)
					
				    context.commit(PREVIOUS_TRANSACTIONS,previous);
			
			}
		},
		[GET_CLIENTS](context,payload){
			// context.commit(REMOVE_DOCKER);
			if(payload){
				var _client = context.state.all_clients.find(c=>c.client._id==payload.id);
			if(_client){
				if(context.state.all_clients.indexOf(_client)!=context.state.all_clients.length-1)
				{
					var next = context.state.all_clients.filter(c=>c.client._id>payload.id).splice(0,5);
				context.commit(PREVIOUS,next);
				return;
				}
			}
			}
			context.commit('loading',true)
			context.dispatch(FETCH_CLIENTS,payload);
		},
		[CHECK_TOKEN](context,payload){
			// console.log(payload)
			if(payload.success==false || payload.message=="Failed to authenticate token." || payload.message=="No token provided."){
				context.commit(IS_LOGGED_IN,false);
				context.commit("setMessage","Access Denied")
				setTimeout(function(){
					context.commit("setMessage","")
				},5000)
				context.commit('loading',false);
				// debugger;
				// $show_notification("you need to be logged in first")
				router.push("/");
				return;
			}
		},
		[FETCH_CLIENTS](context,payload){
				Vue.axios.post(CLIENTS,
				payload,
				{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				// console.log(response.data)
				context.dispatch(CHECK_TOKEN,response.data);
				if(response.data.clients.length>0)
				context.commit(GET_CLIENTS,response.data.clients);
				context.commit("setError",!response.data.status)
				context.commit("loading",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[LOGIN](context,payload){
			console.log(payload);
			context.commit('loading',true);
					Vue.axios.post(CLIENT_LOGIN,
				payload)
			.then((response)=>{
				// console.log(response.data)
				// context.dispatch(CHECK_TOKEN,response.data);
				if(response.data.status){
				localStorage.setItem("token",response.data.token)
				context.commit(LOGIN,response.data);
				context.commit('loading',false);
				router.push("/dashboard/clients");	

				}
				context.commit("setMessage",response.data.message)
				context.commit("loading",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[GET_TRANSACTION](context,payload){
			// context.commit(REMOVE_DOCKER);
			if(payload.id){
				var clients = context.getters.all_transactions(payload.team_Id);
				var _client = clients.find(c=>c._id==payload.id);
			if(_client){
				if(clients.indexOf(_client)!=clients.length-1)
				{
					var next =clients.filter(c=>c._id>payload.id).splice(0,5);
				    context.commit(PREVIOUS_TRANSACTIONS,next);
				   return;
				}
			}

			}
			context.commit('transactions',true)
			Vue.axios.post(TRANSACTION+payload.team_Id,
				{id:payload.id},
				{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				// console.log(response.data)
				context.dispatch(CHECK_TOKEN,response.data);
				if(response.data.transactions.length>0)
				context.commit(GET_TRANSACTION,response.data.transactions);
				context.commit("setError",!response.data.status)
				context.commit("transactions",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('transactions',false);
			})
		},
		[DELETE_CLIENT](context,payload){
			// context.commit(REMOVE_DOCKER);
			var self = this;
			context.commit('loading',true)
			Vue.axios.get(CLIENTS+"/delete/"+payload.client.team_Id,{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.dispatch(CHECK_TOKEN,response.data);
				if(response.status)
				context.commit(DELETE_CLIENT,payload);
				Vue.prototype.$show_notification.$show_notification(response.data.message);
				context.commit("setError",response.data.status)
				context.commit("loading",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
				// self.$show_notification(response.data.message);
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[DELETE_SKILL](context,payload){
			// context.commit(REMOVE_DOCKER);
			context.commit('loading',true)
			Vue.axios.get(SKILLS+"/"+payload._id,{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.dispatch(CHECK_TOKEN,response.data);
				if(response.status)
				context.commit(DELETE_SKILL,payload);
				context.commit("setError",response.data.status)
				context.commit("loading",false)

			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[ADD_SKILL](context,payload){
			context.commit('loading',true);
				return	Vue.axios.post(SKILLS,
					payload,
				{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.dispatch(CHECK_TOKEN,response.data);
				// context.commit("setMessage",r)
  				context.commit("setError",!response.data.status)
  				context.commit('loading',false);
  				context.commit(ADD_SKILL,response.data.message);
			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		}
		,
		[GET_SKILLS](context,payload){
			context.commit('loading',true);
			return	Vue.axios.get(SKILLS,{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.dispatch(CHECK_TOKEN,response.data);
				// context.commit("setMessage",r)

  				context.commit("setError",!response.data.status)
  				context.commit('loading',false);
  				context.commit(GET_SKILLS,response.data.data);
			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[GET_DOCKER](context){
			context.commit('loading',true);
		return	Vue.axios.get(RETRIEVE_DOCKER,{
					'headers':{'x-access-token':context.state.token}
				})
			.then((response)=>{
				context.dispatch(CHECK_TOKEN,response.data);
				// context.commit("setMessage",r
  				context.commit("setError",!response.data.status)
  				context.commit('loading',false);
  				context.commit(GET_DOCKER,response.data.message);
			})
			.catch(error=>{
				// context.commit("setMessage",error)
  				context.commit("setError",true)
  				context.commit('loading',false);
			})
		},
		[NEW_DOCKER](context,payload){
			// debugger;
			context.commit('loading',true)
		return Vue.axios.post(ADD_DOCKER,
				payload,
				{
					'headers':{'x-access-token':context.state.token}
				})
				.then((response) => {
					context.dispatch(CHECK_TOKEN,response.data);
  					context.commit("setMessage",response.data.message)
  					context.commit("setError",!response.data.status)
  					context.commit('loading',false);
  					if(!payload.Id)
  					context.commit(NEW_DOCKER,response.data.docker)
  					// debugger;

				})
				.catch(error=>{
					context.commit("setMessage",error)
  					context.commit("setError",true)
  					context.commit('loading',false);
				})
		}
		// reloadStore(context,payload){
		// 	Vue.axios.get(ADD_DOCKER+payload.tea)
		// }
	},
	modules: {
		landing,
		pricing,
		features,
		faqs,
	}
})
Vue.component('Editor', Editor)
new Vue({
  el: '#app',
  router,
  components: { App, Login , Dashboard , Header,Notifications},
  template: '<App/>',
  store: store
})



