<template>
	<div>
	
			<br>
			<div class="row" >
				<div class="col-md-8"></div>
				<div class="col-md-4">
					<!-- <br> -->
					<input type="" placeholder="Search"    class="form-control" v-model="company" >
					 
				</div>
			
				</div> 
		<!-- </div> -->
	<!-- <div class="row"> -->
		<br>
		<div class="row">
		<!-- <div class="col-md-3"></div> -->
		<div class="col-md-12">
			<table class="table">
			<thead>
				<!-- {{all_clients.length}} -->
				<!-- <th>SN</th> -->
				<th>Company</th>
				<th>Balance</th>
				<th>Payout</th>
				<!-- <th>Earnings</th> -->
				<th>Pending</th>
				<th>Bill</th>
				<th>Members</th>
				<th></th>
				<th></th>
				
			</thead>
			<tbody>
			<!-- 		<tr>
				<td>Javascrip</td>
				<td>
					<i @click.stop="delete_docker(docker)" class="ion-ios-trash-outline docker-action " ></i>
				</td>
			</tr> -->
			<!-- {{filteredClients.length}} -->
					<tr v-for="(client,index) in filteredClients" v-if="filteredClients.length>0" >
						<!-- <td><span>{{index+1}}</span></td> -->
							<td>
					<span>{{client.client.team_name}}</span>

					<!-- <i @click.stop="delete_skill(skill)" class="ion-ios-trash-outline delete " ></i> -->
				</td>
				<td>
					<span>{{client.wallet.balance || "-"}}</span>
				</td>
				<td>
					<span>{{client.wallet.payout || "-"}}</span>
				</td>
				<!-- <td>
					<span>{{client.wallet.earnings || 0}}</span>
				</td> -->
				<td>
					<span>{{client.wallet.pending || "-" }}</span>
				</td>
				<td>
					<span>{{client.bill.amount || "-" }}</span>
				</td>
				<!-- <td>{{client.first_name+" "+client.last_name}}</td> -->
				<td>
					<span style="cursor:pointer" @click="showTeam(client)">{{client.team.length}}
					<i style="font-size:30px;" class="ion-person-stalker"></i>
					</span>
					<!-- <i @click.stop="delete_skill(skill)" class="ion-ios-trash-outline delete " ></i> -->
				</td>
				<td>
					<button class="btn btn-default my-btn-skill pull-left"  @click="showTransaction(client)">
						Transactions
					</button>
				</td>
				<td>
					<i  class="ion-ios-trash-outline"  style="font-size:30px;cursor:pointer" @click="delete_client(client)"></i>
				</td>
			</tr>
			<tr v-if="filteredClients.length==0 && !isLoading">
			<td colspan="5" align="center">
				<!-- <i class="ion-document-text" style="font-size:100px"></i><br> -->
			<h3 align="center">No Client Found!</h3>
			</td>
			</tr>

			</tbody>
		
		</table>
		
		</div>
		<div class="col-md-12" align="center" v-show="filteredClients.length>0">
			<button class="btn btn-primary my-btn-skill" @click="previous(clients[0].client._id)" v-show="all_clients.length>5"><i class="ion-arrow-left-c"></i></button>
		<button class="btn btn-primary my-btn-skill" @click="next(clients[clients.length-1].client._id)"><i class="ion-arrow-right-c"></i></button>
		</div>
		</div>
		<!-- <Modal :show="newSkill"  @close="closeModal"></Modal> -->
			<Modal :show="show" :team="team" @close="closeModal"></Modal>
			<Transaction :show="showCompany" :company="company_Id" @close="closeCompanyModal"></Transaction>

	</div>
</template>
<script type="text/javascript">
import {mapGetters,mapActions} from 'vuex';
import Modal from '@/components/teamModal'
import Transaction from '@/components/transactionModal'
import {GET_CLIENTS,PREVIOUS,DELETE_CLIENT,GET_TRANSACTION,CLEAR_TRANSACTION,REFRESH_CLIENTS,PREVIOUS_TRANSACTIONS} from '../mutation-types'
	export default{
		data(){
			return{
				team:'',
				show:false,
				company:'',
				isSearching:false,
				company_Id:'',
				showCompany:false
			}
		},
			computed:{
			...mapGetters([
			'isLoading',
			
			'error'
			]),
			...mapGetters({
			'clients':'getClients',
			'all_clients':'all_clients'
			}),
			filteredClients(){
				if(this.company=="")
					return this.clients;
				  return this.all_clients.filter(client => {
			        return client.client.team_name.toLowerCase().includes(this.company.toLowerCase())
			      })
			}
			,
		},
		components:{
			Modal,
			Transaction
		},
	
		mounted(){
			if(this.all_clients.length>0)
			{
				this.$store.dispatch(REFRESH_CLIENTS); 
				return;
			}
			this.$store.dispatch(GET_CLIENTS); 
			
		}
		,
		methods:{
			closeModal(){
				this.show = false;
			}
			,
			closeCompanyModal(){
				this.showCompany = false;
				// this.$store.dispatch(CLEAR_TRANSACTION);
			},	
			showTeam(team){
				this.show = true;
				this.team = team;
			},
			showTransaction(company){
				this.showCompany = true;
				this.company_Id = company;
				// console.log(this.$store.getters.all_transactions(company.client.Id).length);
				if(this.$store.getters.all_transactions(company.client.Id).length>0)
				{
					var transaction = this.$store.getters.all_transactions(company.client.Id).splice(0,5);
					this.$store.commit(PREVIOUS_TRANSACTIONS,transaction)
					return;	
				}
				this.$store.dispatch(GET_TRANSACTION,{team_Id:company.client.Id}); 

			},
			next(id){
			this.$store.dispatch(GET_CLIENTS,{id:id}); 
			},
			delete_client(client){
				if(confirm("do you want to delete this company?")){
			    this.$store.dispatch(DELETE_CLIENT,client);
				}
			},
			previous(id){
				// console.log(id)
				if(this.all_clients[0].client._id==id)
     			 return;
				this.$store.dispatch(PREVIOUS,{id:id});
				// this.clients = this._previous;

			}
		}
	}
</script>
<style type="text/css">

	.my-btn-skill{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	/*width: 30%;*/


}
	
	.add{
		display: block !important;
		float:right;
		/*margin-right: 20px;*/
		/*font-size: 50px;*/
		color:#007bff;
		/*border-radius: 100px;*/
		/*box-shadow: 1px 1px 8px #ccc*/
	}
	.delete{
		font-size: 20px;
		color: #000;
	}
	.delete:hover{
		cursor: pointer;
		font-size: 25px;
	}
	
</style>


