<template>
	<div>
	
			<br>
			<div class="row" >
				<div class="col-md-8"></div>
				<div class="col-md-4">
					<!-- <br> -->
					<input type="" placeholder="Search"    class="form-control" v-model="key"  >
					 
				</div>
			
				</div> 
			<div class="row" >
				<!-- <div class="col-md-2"></div> -->
				<div class="col-md-4">
					<!-- <br> -->
					<input type="" v-model="skill" placeholder="Skill" v-validate="'required'" class="form-control" name="skill" >
					  <span align="center" style="color:red;font-size:15px" v-show="errors.has('skill')" class="help is-danger">{{ errors.first('skill') }}</span>
					   <span align="center" style="color:red;font-size:15px" v-show="exist" class="help is-danger">skill already exist</span>
				</div>
				<div class="col-md-2">
				<button style="margin-bottom:15px" @click="addSkill"  class="btn btn-primary my-btn-skill">Add</button>
				<!-- <button style="margin-bottom:5px" v-show="isLoading" disabled="true" class="btn btn-primary my-btn">Loading..</button>	 -->
				</div>
				</div> 
		<!-- </div> -->
	<!-- <div class="row"> -->
		<div class="row">
		<!-- <div class="col-md-3"></div> -->
		<div class="col-md-12">
			<table class="table">
			<thead>
				<!-- {{skills}} -->
				<th>SN</th>
				<th>Skill</th>
				<th>Delete</th>
				
			</thead>
			<tbody class="paginate-skill">
			<!-- 		<tr>
				<td>Javascrip</td>
				<td>
					<i @click.stop="delete_docker(docker)" class="ion-ios-trash-outline docker-action " ></i>
				</td>
			</tr> -->
					<tr v-for="(skill,index) in filteredSkills">
						<td><span>{{index+1}}</span></td>
				<td>{{skill.name}}</td>
				<td>
					<i @click.stop="delete_skill(skill)" class="ion-ios-trash-outline delete " ></i>
				</td>
			</tr>
			<tr v-if="filteredSkills.length==0 && !isLoading">
              <td colspan="4" align="center">
                <span>Not found</span>
              </td>
            </tr>
			</tbody>
		
		</table>
		<template>
<!--   <paginate
    :page-count="20"
    :page-range="3"
    :margin-pages="2"
    :click-handler="clickCallback"
    :prev-text="'Prev'"
    :next-text="'Next'"
    :container-class="'paginate-skills'"
    :page-class="'paginate-skills'">
  </paginate> -->
</template>
		</div>
		</div>
		<!-- <Modal :show="newSkill"  @close="closeModal"></Modal> -->

	</div>
</template>
<script type="text/javascript">
import {mapGetters,mapActions} from 'vuex';
// import Modal from '@/components/skillsModal'
import {ADD_SKILL,GET_SKILLS,DELETE_SKILL} from '../mutation-types'
	export default{
		data(){
			return{
				skill :'',
				exist:false,
				key:''
				// skills:[]
			}
		},
			computed:{
		...mapGetters([
			'isLoading',
			
			'error'
			]),
			...mapGetters({
			'skills':'getSkills',
			}),
			filteredSkills(){

				  return this.skills.filter(skill => {
			        return skill.name.toLowerCase().includes(this.key.toLowerCase())
			      })
			}
	},
		components:{
			// Modal
		},
	
		mounted(){
			if(this.skills.length>0)
				return;
			this.$store.dispatch(GET_SKILLS); 
		}
		,
		methods:{
			closeModal(){
				this.newSkill = false;
			},
			delete_skill(skill){
				var self = this;
				if(confirm("Do you wish to continue?")){
					self.$store.dispatch(DELETE_SKILL,skill)
				}
			},
			addSkill(){
					var self = this;
			self.$validator.validateAll()
			.then(result=>{
				if(result){
					console.log(self.$validator)
					// self.skills.push(self.skill);

					if(self.skills.find(s=>s.name==self.skill)){
						self.exist = true;
						setTimeout(function(){
							self.exist = false;
						},2000)
					return	
					}
					self.$store.dispatch(ADD_SKILL,{skill:self.skill});	
					self.skill='';
					self.exist = false;
					self.$validator.reset();
					

				}
			})
			}
		}
	}
</script>
<style type="text/css">

	.my-btn-skill{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	/*width: 30%;*/


}
	
	.add{
		display: block !important;
		float:right;
		/*margin-right: 20px;*/
		/*font-size: 50px;*/
		color:#007bff;
		/*border-radius: 100px;*/
		/*box-shadow: 1px 1px 8px #ccc*/
	}
	.delete{
		font-size: 20px;
		color: #000;
	}
	.delete:hover{
		cursor: pointer;
		font-size: 25px;
	}
	
</style>


