<template>
	<div>
		<div class="row">
			<div class="col-5">
				<h3>Existing Features <button class="btn btn-primary float-right" @click="newItem">Create New</button></h3>

				 <table class="table table-hover">
				  <thead>
				    <tr>
				      <th scope="col">
				      	<el-checkbox v-model="checked"></el-checkbox>
				      </th>
				      <th scope="col">Action</th>
				    </tr>
				  </thead>
				  <tbody>
				    <tr v-for="item in features">
				      <td>
				      	<el-checkbox v-model="checked"></el-checkbox>
				      </td>
				      <td>
				      	<el-button type="button" size="mini" @click="editItem(item)">
				      		<i class="fa fa-edit"></i>
				      	</el-button>
				      	<el-button type="button" size="mini" @click="previewItem(item)">
				      		<i class="fa fa-eye"></i>
				      	</el-button>
				      	<el-button type="button" size="mini" @click="deleteItem(item.Id)">
				      		<i class="fa fa-trash"></i>
				      	</el-button>
				      </td>
				    </tr>
				  </tbody>
				</table>
			</div>
			<div class="col-7" v-if="action">
				<h4 v-if="single.Id == undefined">New Landing Item</h4>
				<h4 v-if="single.Id != undefined">Edit Landing Item</h4>
				<!-- <div class="form-group text-sm-left">
					<label for="heading">Heading</label>
					<el-input type="text" v-model="single.heading" id="heading"/>
				</div>
				<div class="form-group text-sm-left">
					<label for="text">Item Body</label>
					<el-input type="textarea" :rows="4" v-model="single.text" id="text"/>
				</div>
				<div class="form-group text-sm-left">
					<label for="background">Item Background Color</label><br/>
					<el-color-picker v-model="single.background" id="background"/>
				</div> -->
				<Editor :init="{
					plugins: 'wordcount', menubar: false, min_height: 250,
					toolbar: ['undo redo | styleselect | bold italic ','alignleft aligncenter alignright']
				}"  v-model="single.text"></Editor>
				<el-upload style="margin-top: 15px;"
				  class="upload-demo"
				  ref="upload" :on-success="nextStage"
				  :action="$API+'admin/new_landing_item'"
				  :auto-upload="false">
					<el-button slot="trigger" size="small" type="primary">select file</el-button>
				  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">Save This Item</el-button>
				</el-upload>
								
				<div class="form-group text-sm-right">
				</div>				  
			</div>
		</div>
		<el-dialog :visible="preview" width="1000px" :before-close="closeModal" v-if="preview">
		  <div class="row" style="padding: 70px">
		  	<div class="col-8" v-html="single.text">
		  		
		  	</div>
		  	<div class="col-4">
		  		<img :src="$API+'content/'+single.photo" style="width: 240px">
		  	</div>
		  </div>
		</el-dialog>
	</div>
</template>

<script type="text/javascript">
	import {
	  mapGetters,
	  mapActions
	} from 'vuex';
	export default {
		data(){
			return {
				checked: false,
				preview: false,
			}
		},
		methods: {
			...mapActions('features', ['getLanding','setAction','unSetAction','saveItem','setSingle','deletePost']),
			fetchLandingData(){
				this.getLanding(2);
			},
			submitUpload() {
	        	if(this.$refs.upload.uploadFiles.length > 0){
		        	this.$refs.upload.submit();
	        	}else{
	        		if(!this.single.Id){
			        	this.single.Id = new Date().getTime();
			        	this.single.op = 1;
			        	this.single.type = '2';
	        		}else{
	        			this.single.op = 2;
	        		}
	        		// this.single.photo = '';
		        	this.saveItem(this.single).then(()=>{
		        		this.fetchLandingData();
		        		this.setSingle({});
		        	});
	        	}
	        },
	        closeModal(){
	        	this.preview = false;
	        },
	        nextStage(response, file, fileList){
	        	// console.log(response);
	        	if(!this.single.Id){
		        	this.single.Id = response.id;
		        	this.single.op = 1;
		        	this.single.type = '2';
        		}else{
        			this.single.op = 2;
        		}
	        	this.single.photo = response.file;
	        	this.saveItem(this.single).then(()=>{
	        		this.fetchLandingData();
	        		this.setSingle({});
	        	});
	        },
	        editItem(id){
	        	this.setSingle(id);
	        	// console.log(this.landing.find(e=>e.Id == id));
	        	this.setAction();
	        },
	        previewItem(id){
	        	this.setSingle(id);
	        	this.preview = true;
	        	// console.log(id, this.preview);
	        },
	        deleteItem(id){
	        	var self = this;
	        	this.$confirm('Are you sure to Delete this post?')
		          .then(_ => {
		            self.deletePost(id)
		            .then(e=>{
		            	self.fetchLandingData();
		            })
		          })
		          .catch(_ => {});
	        },
	        newItem(){
	        	this.setAction();
	        	this.setSingle({});
	        }
		},
		mounted(){
			this.fetchLandingData();
		},
		computed: {
			...mapGetters('features', ['features','single','action']),
		}
	}
</script>


