<template>
	<div>
		<div class="row">
			<div class="col-md-1"></div>
			<div class="col-md-10" v-if="isLoggedIn">
				<router-view></router-view>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">
import {mapGetters,mapActions} from 'vuex';
	export default{
	  computed:{
    ...mapGetters([
      'isLoading',
      'isLoggedIn',
      'error'
      ]),

  },
  beforeCreate(){
    var token = localStorage.getItem("token");
    if(token){
      this.$store.commit(LOGIN,{token:token,status:true})
    }
    else{
      this.$router.push("/")
    }
  }
}
</script>



