<template>
	<div> <br>
			<div class="row" >
				<div class="col-md-8"></div>
				<div class="col-md-4">
					<!-- <br> -->
					<input type="" placeholder="Search"    class="form-control" v-model="key"  >
					 
				</div>
			
				</div> 
			
			<div class="row" >
				<div class="col-md-11">
					
				<!-- </div>
				<div class="col-md-1"> -->
				<!-- <i  class="ion-ios-plus add"></i> -->
				<button class="btn btn-primary my-btn-docker" @click="openModal">Add Docker</button>
					
				</div>
				</div> 
		<!-- </div> -->
	<!-- <div class="row"> -->
		<div class="row">
			<!-- {{dockers}} -->
			<!-- <el-dialog :show="true">sdsdsd</el-dialog> -->
			<div v-for="docker in filteredDocker" class="col-md-2 docker" @click="removeDocker">
			<!-- <div class="row"> -->
				<span>{{docker.name}}</span>
			<!-- </div> -->
			<br>
			<br>
			<div class="row">
<div class="col-md-3">
</div>

<div class="col-md-8" align="right">
	<i @click.stop="view(docker)" class="ion-eye docker-action "></i>

		<i @click.stop="edit(docker)" class="ion-edit docker-action "></i>
	<i @click.stop="delete_docker(docker)" class="ion-ios-trash-outline docker-action " ></i>
</div>
			</div>
			</div>
			<div class="row" v-if="filteredDocker.length==0 && !isLoading" >
				<div class="col-md-12" align="center">
					<span align="center">No Docker Found</span>
				</div>	
			</div>
		
		</div>
		<Modal :show="newDocker" :viewDocker="viewDocker" :docker="docker" @close="closeModal"></Modal>

	</div>
</template>
<script type="text/javascript">
import {mapGetters,mapActions} from 'vuex';
import Modal from '@/components/addDockerModal'
import {GET_DOCKER,DELETE_DOCKER} from '../mutation-types'
	export default{
		data(){
			return{
				newDocker :false,
				docker:{},
				viewDocker:false,
				key:''
			}
		},
		components:{
			Modal
		},
		computed:{
			...mapGetters({
				'dockers':'getDockers',
				'isLoading':'isLoading'

				}),
			filteredDocker(){
				  return this.dockers.filter(docker => {
			        return docker.name.toLowerCase().includes(this.key.toLowerCase())
			      })
			}

		

			
		}
		,
		mounted(){
			if(this.dockers.length>0)
				return;
			this.$store.dispatch(GET_DOCKER);
		}
		,
		methods:{
			removeDocker(){
				this.$store.dispatch("removeDocker");
			},
			addDocker(){
				this.$store.dispatch('addDocker',[]);
			},
			closeModal(){
				this.newDocker = false;
				this.viewDocker = false;
				this.docker = {team_Id:0}
			},
			openModal(){
				this.newDocker = true;
			},
			view(docker){
				// this.selectedDocker = 
				this.docker = docker;
				this.viewDocker = true;
				this.newDocker = true;
			},
			edit(docker){
				this.docker = docker;
				this.newDocker = true;
			},
			delete_docker(docker){
				var response = confirm("Do you want to delete this docker file?")
				if(response){
					// alert();
					this.$store.dispatch(DELETE_DOCKER,docker)
				}
			}
		}
	}
</script>
<style type="text/css">
/*	table{
		font-size: 10px !important;
	}*/
	.my-btn-docker{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	/*width: 30%;*/


}
	.docker{
		box-shadow: 1px 1px 8px #ccc;
		margin: 15px;
		height: 100px;
		padding:10px
	}
	.docker-action{
		margin-left: 10px;
		font-size: 20px;
		cursor: pointer;
	}
	.add{
		display: block !important;
		float:right;
		/*margin-right: 20px;*/
		font-size: 50px;
		color:#007bff;
		/*border-radius: 100px;*/
		/*box-shadow: 1px 1px 8px #ccc*/
	}
</style>


