<template>
	  <transition name="modal">
    <div class="modal-mask" v-if="show">
      <div class="modal-wrapper">
        <div class="modal-container">
        	<span class="modal-close" @click="$emit('close')">X</span>
          <div v-if="viewDocker" class="modal-header">
          
          <h5 > View Docker</h5>
          </div>
            <div v-else class="modal-header">
          
          <h5 v-if="docker.Id"> Update Docker</h5>
          <h5 v-else> Add new Docker</h5>
          </div>
<!-- {{docker}} -->
          <div class="modal-body">
          <!-- <form  @submit.prevent="add"> -->
          	<div v-if="viewDocker">
          		<input  class="form-control" :value="docker.name" readonly="true"></input>
          		<br>
          		<br>
          	</div>
          	<div v-else class="form-group">
          		<label for="dockerName">Docker Name</label>
          		<input v-validate="'required'"  v-model="docker.name" class="form-control" placeholder="Docker Name" name="dockerName"></input>
          		 <i v-show="errors.has('dockerName')" class="fa fa-warning"></i>
                        <span align="center" style="color:red;font-size:15px" v-show="errors.has('dockerName')" class="help is-danger">{{ errors.first('dockerName') }}</span>
          	</div>	
          	<div v-if="viewDocker">
          		<textarea readonly="true"  class="form-control docker-content" rows="5" >{{docker.content}}</textarea>
          	</div>
          	<div v-else class="form-group">
          		<label for="dockerContent">Docker Content</label>
          		<textarea v-validate="'required'" name="dockerContent" v-model = "docker.content"  class="form-control docker-content" rows="5" ></textarea>
          		 <i v-show="errors.has('dockerContent')" class="fa fa-warning"></i>
                        <span align="center" style="color:red;font-size:15px" v-show="errors.has('dockerContent')" class="help is-danger">{{ errors.first('dockerContent') }}</span>
          	</div>
          	 
          <!-- </form> -->
          </div>
 <div v-if="isLoading" class="modal-footer">
           	<Spinner></Spinner>  
      <!-- asas	 -->
          </div>
          <div  v-show="!viewDocker"   v-else class="modal-footer" >
          	   <button v-if="docker.Id" @click="add" class="btn btn default my-btn" >
                Update
              </button>
                <button v-else @click="add" class="btn btn default my-btn" >
                Add
              </button>
              <!-- <button @click="$emit('close')">close</button> -->
          </div>
        
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/javascript">
import Spinner from '@/components/spinner';
import {NEW_DOCKER,REMOVE_DOCKER} from '../mutation-types'
// import {ADD_DOCKER} from '../api'
import {mapGetters} from 'vuex';
	export default{
	props:['show','docker','viewDocker'],
	mounted(){
// alert(API)
	},
	components:{
		Spinner
	},
	computed:{
		...mapGetters([
			'isLoading',
			'message',
			'error'
			])
	},
	methods:{
		add(){
			var self = this;
			self.$validator.validateAll()
			.then(result=>{
				if(result){
					self.$store.dispatch(NEW_DOCKER,self.docker)
					.then(response=>{
						self.$show_notification(self.message);
						if(self.error){

						}
						else{
							self.$emit('close')
						}
					})
				}
			})

		}
	}
	}
</script>
<style type="text/css">
.my-btn{
	border-radius: unset !important;
	box-shadow:  1px 1px 8px #ccc;
	width: 30%;


}
.docker-content{
	background-color: #000 !important;
	color: #fff !important;
	/*font-family: italics*/
}
.modal-close{
	float:right;
	color:#000;
	/*border-radius: 20px;*/
	cursor: pointer !important;
	/*background-color: #000*/
		padding:5px;
}
.modal-close:hover{
	box-shadow: 1px 1px 8px #ccc ;


}
.vue-notification .success{
	background-color: #fff !important;
	padding:10px !important;
	box-shadow: 1px 1px 8px #ccc !important;
}
	.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 40%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h5 {
  margin-top: 0;
  /*color: #42b983;*/
 
}
.modal-header{
	 border-bottom: unset !important
}

.modal-body {
  /*margin: 20px 0;*/
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}
.modal-footer{
	border-top: unset !important;
}
.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>


