 import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Dashboard from '@/components/dashboard'
import Login from '@/components/login'
import Skills from '@/components/manage-skills'
import Clients from '@/components/manage-clients'
import Docker from '@/components/Docker'
import Landing from '@/components/landing'
import Features from '@/components/features'
import Pricing from '@/components/pricing'
import FAQS from '@/components/landing/new'
import ExistingLanding from '@/components/landing/existing'

Vue.use(Router)

export default new Router({
  routes: [
     {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
      children:[
      {
      	path:"docker",
      	name:"docker",	
      	component:Docker,
      },
      {
      	path:"skills",
      	name:"skills",	
      	component:Skills,
      },
      {
        path:"landing",
        name:"landing", 
        component: Landing, 
      },
      {
        path:"features",
        name:"features",  
        component:Features,
      },
      {
        path:"pricing",
        name:"pricing",  
        component:Pricing,
      }
      ,
      {
        path:"faqs",
        name:"faqs",  
        component:FAQS,
      }
      ,
      {
        path:"clients",
        name:"clients",  
        component:Clients,
      }


      ]
    },
    {
      path: '/',
      name: 'login',
      component: Login
    }
  ]
})



