<template>
	<div>
		<div class="row">
			<div class="col-5">
				<h3>Existing FAQs 
					<button class="btn btn-primary float-right" @click="newItem">Create New</button>
				</h3>
				 <table class="table table-hover">
				  <thead>
				    <tr>
				      <th scope="col">
				      	<el-checkbox v-model="checked"></el-checkbox>
				      </th>
				      <th scope="col">Action</th>
				    </tr>
				  </thead>
				  <tbody>
				    <tr v-for="item in features">
				      <td>
				      	<el-checkbox v-model="checked"></el-checkbox>
				      </td>
				      <td>
				      	<el-button type="button" size="mini" @click="editItem(item)">
				      		<i class="fa fa-edit"></i>
				      	</el-button>
				      	<el-button type="button" size="mini" @click="previewItem(item)">
				      		<i class="fa fa-eye"></i>
				      	</el-button>
				      	<!-- <el-button type="button" size="mini" @click="deleteItem(item.Id)">
				      		<i class="fa fa-trash"></i>
				      	</el-button> -->
				      </td>
				    </tr>
				  </tbody>
				</table>				
			</div>
			<div class="col-7" v-if="action">
				<h4 v-if="single.Id == undefined">New FAQ Item</h4>
				<h4 v-if="single.Id != undefined">Edit FAQ Item</h4>
				<!-- <div class="form-group text-sm-left">
					<label for="heading">Package Type/Name</label>
					<el-input type="text" v-model="single.type" id="heading"/>
				</div>
				<div class="form-group text-sm-left">
					<label for="heading">Package Amount/month/user</label>
					<el-input type="text" v-model="single.price" id="heading"/>
				</div>
				<div class="form-group text-sm-left">
					<label for="heading">Package features</label>
					<el-input type="text" v-model="feature" id="heading">
						<el-button slot="append" @click="add2single"><span class="fa fa-plus"></span></el-button>
					</el-input>
				</div> -->				
				<Editor :init="{
					plugins: 'wordcount', menubar: false, min_height: 370,
					toolbar: ['undo redo | styleselect | bold italic ','alignleft aligncenter alignright']
				}"  v-model="single.text"></Editor>	
				<div class="form-group text-sm-left" style="margin-top: 15px;">
				  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">Save This Item</el-button>
				</div>				  
			</div>
		</div>
		<el-dialog :visible="preview" width="500px" :before-close="closeModal" v-if="preview">
		  <div class="row" :style="{'backgroundColor': single.background}" style="padding: 70px">
		  	<div class="col-12" v-html="single.text">
		  		
		  	</div>
		  </div>
		</el-dialog>
	</div>
</template>

<script type="text/javascript">
	import {
	  mapGetters,
	  mapActions
	} from 'vuex';
	export default {
		data(){
			return {
				checked: false,
				preview: false,
				feature:'',
				details: '',
			}
		},
		methods: {
			...mapActions('faqs', ['getLanding','setAction','unSetAction','saveItem','setSingle','deletePost']),
			fetchLandingData(){
				this.getLanding(3);
			},
			submitUpload() {
	        		if(!this.single.Id){
			        	this.single.Id = new Date().getTime();
			        	this.single.op = 1;
			        	this.single.type = '3';
	        		}else{
	        			this.single.op = 2;
	        		}
	        		// this.single.photo = '';
		        	this.saveItem(this.single).then(()=>{
		        		this.fetchLandingData();
		        		this.setSingle({});
		        	});
	        },
	        add2single(){
	        	if(!this.single.features){		
	        		this.single.features = [];
	        	}
		        	this.single.features.push(this.feature);
		        	this.feature = '';
	        },
	        closeModal(){
	        	this.preview = false;
	        },
	        nextStage(response, file, fileList){
	        	// console.log(response);
	        	if(!this.single.Id){
		        	this.single.Id = response.id;
		        	this.single.op = 1;
        		}else{
        			this.single.op = 2;
        		}
	        	this.single.photo = response.file;
	        	this.saveItem(this.single).then(()=>{
	        		this.fetchLandingData();
	        		this.setSingle({});
	        	});
	        },
	        editItem(id){
	        	this.setSingle(id);
	        	// console.log(this.landing.find(e=>e.Id == id));
	        	this.setAction();
	        },
	        previewItem(id){
	        	this.setSingle(id);
	        	this.preview = true;
	        	// console.log(id, this.preview);
	        },
	        deleteItem(id){
	        	var self = this;
	        	this.$confirm('Are you sure to Delete this post?')
		          .then(_ => {
		            self.deletePost(id)
		            .then(e=>{
		            	self.fetchLandingData();
		            })
		          })
		          .catch(_ => {});
	        },
	        newItem(){
	        	this.setAction();
	        	this.setSingle({});
	        }
		},
		mounted(){

			this.fetchLandingData();
		},
		computed: {
			...mapGetters('faqs', ['features','single','action']),
		}
	}
</script>


