var port = '';
//port = 8000; //test
//port = 8001; //demo
//port = 8002; //live
var prot = 'https://';
var hostname = process.env.LOCAL===1?'localhost':location.hostname;
hostname = hostname.toLowerCase();
if(hostname == "zeedas.com" || hostname == "www.zeedas.com"){
    port = 8002;
}else if(hostname == "demo.zeedas.com"){
    port = 8001;
}else{
    hostname = "test.zeedas.com";
    port = 8000;
}
import axios from 'axios';
import Vue from 'vue'
export const API = "https://"+hostname+":"+port+"/";
export const ADD_DOCKER = API+"clients/create_docker"
export var blackAxios = axios.create({
  baseURL: API
});
Vue.prototype.$API = API;


export const RETRIEVE_DOCKER =API+"clients/docker_list/0"

export const REMOVE_DOCKER = API+"clients/remove_docker"

export const SKILLS  = API+"clients/skills"

export const CLIENTS = API+"clients"

export const CLIENT_LOGIN = API	+"clients/login"

export const TRANSACTION = API+"clients/transactions/"


